/*global CSS*/
:root {
  --at-white: #fff;
  --at-black: #000;
  --at-base-color: #2e312f;
  --at-body-color: #9da29f;
  --at-body-bg: #f0f0f1;
  --at-primary: #48b44f;
  --at-primary--hover: #3ea245;
  --at-secondary: #e8f3eb;
  --at-secondary--hover: #c7e6cf;
  --at-tertiary: #a0a0a0;
  --at-tertiary--hover: #7b7979;
  --at-quaternary: #585a59;
  --at-quaternary--hover: #414141;
  --at-quinary: #f6f7f7;
  --at-quinary--hover: #eee;
  --at-accent: #f5e5b4;
  --at-accent--hover: #fff6dc;
  --at-heading: #f6f7f7;
  --at-link: #f6f7f7;
  --at-link--hover: #f6f7f7;
  --at-code-color: #d63384;
  --at-border-color: #d3d9de;
  --at-danger: #dc3232;
  --at-danger--hover: #c82333;
  --at-success: #46b450;
  --at-success--hover: #1e7e34;
  --at-info: #00a0d2;
  --at-info--hover: #17a2b8;
  --at-warning: #ffb900;
  --at-warning--hover: #e0a800;
  --at-dark: #000;
  --at-dark--hover: #121212;
  --at-light: #d3d9de;
  --at-light--hover: #eee;
}

[data-at-theme=dark] {
  color-scheme: dark;
  --at-base-color: #d7d7d7;
  --at-body-color: #959595;
  --at-body-bg: #121212;
  --at-secondary: #000;
  --at-secondary--hover: #292c2e;
  --at-border-color: #575757;
}
[data-at-theme=dark] .at-bg-white {
  --at-bg-cl: #1e1e1e;
}
[data-at-theme=dark] .at-bg-grad-1 {
  --at-bg-img: linear-gradient(90deg, #2d2d2d, #1e1e1e);
}

@media (min-width: 576px) {
  .at-ctnr {
    --at-ctnr: 540px;
